@import "owl/owl.carousel";
@import "owl/owl.theme.default"; 
@import "main";

.pager {
    text-align: center;
    margin-bottom: 35px;
    a {
        display: inline-block;
        width: 34px;
        height: 34px;
        
        text-decoration: none;
        color: #394659;
        padding-top: 9px;
        &.active,
        &:hover {
            background: #da2128; 
            color: #fff;
        }
    }
}
.as {
    &_block {
        padding: 28px;
        background: #edf1f7; 
        margin-bottom: 85px;
    }
    &_search {
        position: relative;
        form {
            padding: 0;
            &:after,
            &:before {
                display: none;
            } 
        }
        input {
            font-size: 16px;
            padding: 12px 35px 12px 19px;
            @include plhl {
                font-size: 16px;
                color: #394659;
            }
        }
        button {
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0; 
            background: none;
            font-size: 23px;
            padding-right: 10px; 
        }
    }
    &_news {
        a {
            display: block;
            margin-bottom: 29px;
            font-size: 16px;
            color: #394659;
            text-decoration: none;
            &:hover {
                text-decoration: underline;
                b {
                    text-decoration: none;
                }
            }
        }
        b {
            font-family: $bold;
            display: block;
            margin-bottom: 5px;
        }
    }
    &_head {
        font-size: 24px;
        font-family: $bold;
        position: relative; 
        margin-bottom: 30px;
        &:before {
            content: '';
            position: absolute;
            width: 15px;
            height: 16px;
            background: $red;
            display: block;
            left: -28px;
            bottom: 7px;
        }
    }
    &_btn {
        margin: 0 auto;
        display: inline-block;
        max-width: 245px;
        text-align: center;
        color: #fff;
        text-decoration: none;
        padding: 15px 0;
        background: $red; 
        color: #fff!important; 
        text-decoration: none!important; 
        
    }
    &_pop {
        .pop {
            &_item {
                margin-left: auto;
                margin-right: auto;
                margin-bottom: 40px;
                max-width: 275px;
            }
        }
        img {
            width: 100%;
            display: block;
            margin-bottom: 10px;
        }
        a {
            color: #394659;
            font-size: 18px;
            margin-bottom: 10px;
            display: block;
            margin-bottom: 16px;
            text-decoration: none;
            font-family: $bold;
        }
    }
}
