@font-face {
  font-family: 'icomoon';
  src:  url('../fonts/icomoon/icomoon.eot?sa5lut');
  src:  url('../fonts/icomoon/icomoon.eot?sa5lut#iefix') format('embedded-opentype'),
    url('../fonts/icomoon/icomoon.ttf?sa5lut') format('truetype'),
    url('../fonts/icomoon/icomoon.woff?sa5lut') format('woff'),
    url('../fonts/icomoon/icomoon.svg?sa5lut#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

[class^="icon-"], [class*=" icon-"] {
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

