
.about {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
//    padding: 45px 0 50px 0;
    padding-top: 45px;
    padding-bottom: 50px;
    @media (max-width:760px) {
        display: block;
    }
    &_b {
        font-family: $black;
        font-size: 60px;
        line-height: 48px;
        width: 270px;
        min-width: 270px;
        span {
            display: block;
            color: $red;
        }
    }
    &_p {
        font-size: 24px;
        
        padding-top: 10px; 
    }
    &_video {
        position: relative;
        cursor: pointer;
        margin-bottom: 53px;
        display: block;
        img {
            display: block;
            width: 100%;
            
        }
        &:before,
        &:after {
            content: '';
            display: block;
            position: absolute;
            top: 50%;
            left: 50%;
            z-index: 2;
            
            display: block;
        }
        &:before {
            width: 90px;
            height: 90px;
            margin: -45px;
            background: $red;
        }
        &:after {
            width: 0;
            height: 0;
            border: 15px solid rgba(255,255,255,0);
            border-left: 20px solid  #fff;
            margin: -15px 0 0 -7.5px; 
            z-index: 3;
        }
    }
}

.advantages {
    border-bottom: 1px solid #d7d7d7;
    margin-bottom: 70px;
    padding-bottom: 20px;
    @media (max-width:750px) {
        margin-bottom: 30px;
    }
    i {
        font-size: 40px;
        color: $gr;
        margin-right: 30px;
    }
    &_block {
        a {
            padding: 16px 15px 16px 27px;
            display: flex;
            align-items: center;
            font-size: 24px;
            color: $blue;
            text-decoration: none;
            background: #fff;
            @media (max-width:700px) {
                font-size: 18px;
                padding: 0;
                margin-bottom: 10px;
            }
        }
    }
    &.section_lr {
        @media (max-width:750px) {
           display: block;
        }
    }
    .left {
        @media (max-width:1024px) {
            width: 60%;   
        }
        @media (max-width:750px) {
            width: 100%;   
        }
        
    }
    .right {
        text-align: center;
        @media (max-width:1024px) {
            width: 40%;   
        }
        @media (max-width:750px) {
            width: 100%;   
        }
        img {
            margin-top: -90px;
            margin-bottom: -30px;
            @media (max-width:1024px) {
                margin: 0;
            }
            @media (max-width:500px) {
                width: 70%;
            }
        }
    }
}

.more {
    .section_head {
        position: relative; 
        &:after,
        &:before {
            content: '';
            width: 200px;
            height: 10px;
            background: #da2128;
            position: absolute;
            bottom: 15px;
            right: 0;
            
        }
        &:after {
            right: 205px;
            width: 10px;
        }
        @media (max-width:870px) {
            &:after,
            &:before {
                display: none;
            }
        }
    }
    p {
        font-size: 24px;
        color: $gr;
        
    }
}

.worth {
    margin-bottom: 157px;
    @media (max-width:800px) {   
        margin-bottom: 30px;
    }
    @media (max-width:600px) {
        margin-left: -15px;  
        margin-right: -15px;
    }
    &_tab {
        a {
            padding: 16px 15px 16px 27px;
            display: flex;
            align-items: center;
            font-size: 24px;
            color: $gr; 
            text-decoration: none;
            background: #fff; 
            box-shadow: 0 0 18px rgba(0,0,0,.15);
            @media (max-width:900px) {
                font-size: 18px;
                padding-right: 22px;
            }
            & + a {
                margin-top: 20px;
            }
            i {
                font-size:40px;
                color: $blue; 
                margin-right: 30px;
            }
            &.active {
                position: relative;
                &:after {
                    content: '';
                    display: block;
                    position: absolute;
                    top: 50%;
                    width: 20px;
                    height: 20px;
                    margin-top: -10px;
                    right: 0;
                    background: $red;
                }
            }
            span {
                @media (max-width:680px) {
                    display: none; 
                }
            }
        }
        &s {
            height: 100%;
        }
    }
    &_item {
        padding: 35px 30px;
        background: #f7f7f7;
        font-size: 18px;
        color: $gr;
        display: none;
        height: 100%;
        @media (max-width:600px) {
            font-size: 16px;
            padding: 15px 10px;
        }
        &.active {
            display: block;
        }
    }
    &_head {
        font-size: 24px;
        
        margin-bottom: 30px;
        padding-bottom: 17px;
        position: relative;
        @media (max-width:600px) {
            font-size: 18px;
        }
        &:after,
        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 85px;
            height: 10px;
            bottom: -10px;
            left: 0;
            background: $red;
        }
        &:after {
            left: 90px;
            width: 10px;
        }
    }
    
    @media (max-width:680px) {
        .left {
            min-width: 95px;
            width: 95px;
            margin-right: 15px;
        }
        .right {
            width: 100%;
        }
    }

    
}

.work {
    text-align: center;
    display: flex;
    justify-content: space-between;
    align-items: stretch;
    position: relative;
    font-size: 18px;
    margin-bottom: 150px;
    @media (max-width:800px) {
        display: block;   
        margin-bottom: 30px;
    }
    &_text {
        font-family: $black;
        min-height: 90px;
        font-size: 24px;
        @media (max-width:900px) {
            font-size: 18px;
        }
        @media (max-width:800px) {
            min-height: 0;
            margin-top: 60px;
        }
    }
    &_item {
        width: 30%;
        @media (max-width:750px) {
            width: 100%;
        }
        & + & {;
            .work_img {
                position: relative;
                z-index: 5;
                 &:before {
                    content: '';
                    display: block;
                    height: 10px;
                    position: absolute;
                    top: 50%;
                    margin-top: -10px;
                    width: 176px;
                    z-index: 1; 
                    left: -200px;  
                    background: url(../images/w_b.png); 
                    @media (max-width:1090px) {
                        width: 130px;
                        left: -165px;
                    }
                    @media (max-width:930px) {
                        width: 95px;
                        left: -120px;
                    }
                    @media (max-width:860px) {
                        width: 95px;
                        left: -100px;
                    }
                    @media (max-width:830px) {
                        width: 60px;
                        left: -82px;
                    }
                    @media (max-width:750px) {
                        background: url(../images/w_b_mob.png);
                        position: absolute;
                        width: 10px;
                        height: 34px;
                        margin: -16px auto 18px -5px;
                        left: 50%;
                        top: -63px;
                    }
                    
                }
            }
        }
    }
    &_img {
        max-width: 160px;
        margin: 0 auto;
        background: #fff;
        position: relative;
    }
}

.faq {
    margin-bottom: 130px;
    @media (max-width:750px) { 
        margin-bottom: 30px;
    }
    &_item {
        padding: 27px;
        font-size: 18px;
        color: $gr;
        cursor: pointer;
        margin-bottom: 1px;
        &.open {
            background: #ededed;
            .faq {
                &_head {
                    color: $red;
                    i {
                        transform: rotate(90deg); 
                    }
                }
                
            }
        }
        &:nth-child(4) {
            & ~ * {
                display: none;
            }
        }
    }
    &_head {
        font-size: 24px;
        font-family: $bold;
        position: relative;
        padding-right: 25px;
        i {
            position: absolute;
            color: $gr;
            display: block;
            top: 0px;
            right: 0px; 
            transform: rotate(-90deg);
            
            transition: all ease 0.4s;
        }
        span {
            color: $red;
        }
    }
    &_text {
        display: none;
        padding-top: 20px;
    }
    &_more {
        padding: 15px 0;
        background: #ededed;
        color: $red;
        text-align: center;
        font-size: 16px;
        cursor: pointer;
    }
}

.slider {
    .wrapper {
        padding-bottom: 50px;
//        border-bottom: 1px solid #d7d7d7;
        margin-bottom: 60px;
    }
    .section_head {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    &_nav {
        font-size: 15px;
        display: flex;
        a {
            width: 30px;
            height: 30px;
            display: flex;
            justify-content: center;
            align-items: center; 
            background: red;
            color: #fff;
            
            text-decoration: none;
            transition: all ease 0.4s;
            &:hover {
                background: $blue;
            }
            & + a {
                margin-left: 15px;
            }
            &.slider_right {
                transform: rotate(-180deg);
            }
//            &.slider_left {
//                transform: rotate(90deg);
//            }
        }
    }
    &_item {
        img {
            z-index: 1;
        }
        span {
                padding: 20px;
            margin-left: 25px;
            margin-right: 25px;
            margin-top: -70px;
            background: $red;
            position: relative;
            z-index: 2;
            color: #fff;
            font-size: 18px;
            
            display: flex;
            justify-content: center;
            align-items: center;
        }
        b {
            display: block;
            font-size: 24px;
            font-family: $bold;
        }
    }
    img {
        width: 100%;
    }
}

