.footer {
    &_top {
        background: #5d6b80;
        padding-top: 12px;
        padding-bottom: 12px;
        position: relative;
        z-index: 2;
        box-shadow: 5px 5px 12px rgba(0,0,0,.19);
        .wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
        }
        form {
            &:before,
            &:after {
                display: none;
            }
            background: none;
            padding: 0;
            display: flex;
            justify-content: space-between;
            align-items: center;
            @media (max-width:720px) {
                display: block;
                width: 100%;
                text-align: center;
            }
        }
        button {
            font-size: 20px;
            white-space: nowrap;
            padding-top: 20px;
            padding-bottom: 20px;
        }
        input {
            margin-bottom: 0;
            margin-right: 15px;
            padding-top: 12px;
            padding-bottom: 12px;
            @media (max-width:800px) and (min-width:720px) {
                display: none;
            }
            @media (max-width:720px) {
                margin-bottom: 15px;
                text-align: center;
            }
        }
        .tel {
            display: none;
            color: #fff;
            margin-right: 15px;
            a {
                color: #fff;
            }
            @media (max-width:800px) {
                display: block
            }
            @media (max-width:720px) {
                display: flex;
                margin-bottom: 15px;
                justify-content: space-between;
                align-items: center;
            }
        }
    }
    &_social {
        color: #ffffff;
        font-size: 18px;
        @media (max-width:720px) {
            display: none;
        }
        div {
            display: flex;
            align-items: center;
            & + div {
                margin-top: 7px;
            }
        }
        a {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 40px;
            height: 40px;
            background: #6d65ad;
            color: #fff;
            text-decoration: none;
            margin-right: 12px;
            &.telega {
                background: #60a8dd;
            }
            &.fb {
                background: #008bce;
            }
            &.whatsapp {
                background: #2bb642;
            }
        }
    }
}
.p_form {
    display: none;
    form {
        text-align: center;
        p {
            span {
                color: $red;
                display: block;
            }
        }
        &:before,
        &:after {
            top: -25px;
        }
        &:after {
            right: 0;
            width: auto;
            
        }
    }
}
.fancybox-content {
    overflow:visible!important; 
    padding: 0!important; 
}
.blog {
    border-top: 1px solid #d7d7d7;
    &_tabs {
        margin-bottom: 14px;
        a { 
            display: inline-block;
            padding: 12px 15px;
            text-decoration: none;
            color: #394659;
            font-size: 14px;
            text-transform: uppercase;
            margin-bottom: 10px;
            &:hover,
            &.active {
                background: #da2128;
                color: #fff;
            }
        }
    }
    &_in {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
        flex-wrap: wrap;
    }
    &_item {
        width: 100%;
        position: relative;
        padding-bottom: 34px;
        @media (min-width:580px) {
            display: flex;
            align-items: stretch;
            justify-content: space-between;
        }
        & > div {
            width: 100%;
        }
        a {
            display: block;
        }
        img {
            @media (min-width:580px) {
                max-width: 275px;
                min-width: 275px;
            }
            width: 100%;
            
        }
        &.big {
            width: 100%;
            @media (min-width:880px) {
                img {
                    max-width: 100%;
                    margin-right: 0;
                    min-width: 0;
                }
                .blog {
                    &_name {
                        font-size: 30px;
                    }
                }
                p {
                    display: none;
                }
            }
        }
        &.average {
            width: 48%;
            
            
            @media (max-width:970px) {
                width: 100%;
            }
            @media (min-width:880px) {
                img {
                    max-width: 100%;
                    min-width: 0;
                    margin-right: 0;
                }
                p {
                    display: none;
                }
            }
        }
        @media (min-width:880px) {
            &.blog_item_col {
                display: block;
            }
        }
        p {
            display: block;
            margin-bottom: 10px;
        }
    }
    &_img {
        
        margin-bottom: 14px;
        @media (min-width:580px) {
            margin-right: 20px;
        }
    }
    &_cat {
        background:#da2128; 
        position: absolute;
        top: 0;
        left: 0;
        padding: 1px 12px;
        color: #fff;
        text-decoration: none;
        font-size: 14px;
        &:after {
            content: '';
            display: block;
            top: 0;
            bottom: 0;
            width: 20px;
            left: calc( 100% + 6px );
            background: #da2128; 
            position: absolute;
        }
    }
    &_name {
        color: #394659;
        font-size: 24px;
        font-family: $bold;
        text-decoration: none;
        margin-bottom: 15px; 
        &:hover {
            color: $red;
        }
    }
    &_info {
        font-size: 16px;
        color: #394659;
        i {
            margin-right: 5px;
            display: inline-block;
        }
        span {
            display: inline-block;
            margin-right: 10px;
            ~ span {
                font-size: 14px;
            }
        }
    }
    &_content {
        font-size: 16px;
        
        .blog {
            &_info {
               margin-bottom: 20px; 
            }
            &_cat {
                position: relative;
                display: inline-block;
            }
        }
        & > * {
            margin-bottom: 20px;
        }
        
        h1,
        h2,
        h3,
        h4 {
            font-family: $bold;
        }
        h1 {
            font-size: 36px;
        }
        h2 {
            font-size: 24px; 
        }
        h4,
        h3 {
            font-size: 18px;
        }
        img {
            width: 100%;
            &[align="left"],
            &[align="right"] {
                width: auto;
                margin: 0 29px;
                & + * {
                    &:after {
                        content: '';
                        display: block;
                        clear: both;
                        width: 1px;
                    }
                }
                
            }
            &[align="left"] {
                margin-left: 0;
            }
            &[align="right"] {
                margin-right: 0;   
            }
        }
        blockquote {
            background: #edf1f7;
            &:not(.different) {
                background: #edf1f7  url(../images/bq.png) no-repeat 20px 20px;
                padding: 20px 20px 20px 66px;
                b {
                    color: #788598;
                    display: block;
                    text-align: right;
                    margin-top: 10px;
                    font-family: 'PFCentroSansPro-Regular';
                    font-weight: normal;
                }
            }
            &.different {
                padding: 12px 23px;
                b {
                    display: block;
                }
                &.red {
                    background: #fededf;
                    color: #394659;
                }
                &.blue {
                    color: #6d7a8e;
                }
            }
        }
        ul {
            li {
                position: relative;
                margin-bottom: 20px;
                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 6px;
                    height: 6px;
                    background: $red;
                    left: -24px;
                        top: 7px;
                }
            }
        }
        table {
            text-align: left;
            width: 100%;
        }
        td {
            padding: 13px;
            background: #edf1f7;
            border: 2px solid #fff;
        }
        ol {
            li {
                list-style: none;
                position: relative;
                margin-bottom: 17px;
                &:before {
                    position: absolute;
                    top: 0;
                    width: 26px;
                    height: 26px;
                    background: $red;
                    text-align: center;
                    left: -35px;
                    counter-increment: ol;
                    content: counter(ol);
                    
                    color: #fff;
                    padding-top: 3px;
                    top: -3px;
                }
            }
        }
    }
}

.search {
    &_page {
        font-size: 24px;
        b {
            font-family: $bold;
            display: block;
            span {
                color: $red;
            }
        }
        p {
            font-size: 16px;
            padding-bottom: 7px;
        }
    }
}
