header {
    position: relative;
    .wrapper {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-top: 12px;
        padding-bottom: 12px;
        @media (max-width:600px) {
         padding-bottom: 19px;
        }
    }
}
.tel {
    text-align: right;
    i {
        display: block;
        font-size: 14px;
    }
    a {
        text-decoration: none;
        color: $blue;
        font-size: 24px;
        font-family: $bold;
    }
}
.lang {
    color: $blue;
    font-family: $bold;
    font-size: 24px;
    text-decoration: none;
    position: relative;
    padding: 5px;
    a {
        color: $blue;
    font-family: $bold;
    font-size: 24px;
    text-decoration: none;
    }
    &:hover {
        background:#f7f7f7;
        .lng {
            &_dd {
                display: block;
            }
        }
    }
    &:before {
        content: '';
        display: inline-block;
        width: 8px;
        height: 8px;
        background: $red;
        vertical-align: middle;
            margin-bottom: 5px;
    }
}
.header {
    &_block {
        display: flex;
        align-items: center;
        @media (max-width:779px) {
            display: none;
        }
        & > * {
            margin-left: 36px;
        }
        &_mob {
            display: none;
            @media (max-width:779px) {
                display: flex; 
                align-items: center;
            }
            &.open + .header_block {
                display: block;
                position: absolute;
                z-index: 5;
                top: 100%;
                background: #394659;
                left: 0;
                right: 0;
                padding: 20px;
                color: #fff;
                
                text-align: left;
                a {
                    color: #fff;
                }
                .tel {
                    color: #fff;
                    text-align: left;
                    margin-bottom: 15px;
                }
                .lang {
                    display: none;
                }
            }
        }
        &_menu {
            width: 22px;
            height: 17px;
            margin-left: 20px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            flex-direction: column;
            
            cursor: pointer;
            span {
                display: block;
                width: 22px;
                height: 3px;
                background: #9ea5a7;
                transition: transform ease 0.4s;
            }
            &.open {
                justify-content: center;
                span {
                    &:first-child {
                        transform: rotate(45deg);
                        margin-bottom: -3px;
                        & + span {
                            display: none;
                        }
                    }
                    &:last-child {
                        transform: rotate(-45deg);
                    }
                }
            }
        }
    }
}

