@import "variables";
@import "mixins";

html,
body {
	font-family: 'PFCentroSansPro-Regular';
    color: $blue;
    
}
ol {
    counter-reset: ol;
}
.wrapper {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 15px;
}
.btn {
    padding: 17px 40px;
    background: $red;
    color: #fff;
    font-size: 20px;
    display: inline-block;
    text-decoration: none;
    transition: all ease 0.4s;
    @media (max-width:600px) {
        font-size: 18px;
        text-align: center;
        padding: 20px;
    }
    &:hover {
        background: $blue;
    }
}

section {
    background: url(../images/bg.png) no-repeat top center;
    background-size: cover;
    color: #fff;
    text-shadow: 3px 3px 7px rgba(0,0,0,.35);
    padding-top: 19px;
    overflow: hidden;
    @media (max-width:600px) {
        padding-top: 20px;
    }
    
    h1 { 
        font-size: 56px;
        line-height: 50px;
        font-family: $black; 
        @media (max-width:800px) {
            font-size: 48px;
            line-height: 48px;
        }
        @media (max-width:600px) {
            font-size: 28px;
            line-height: 28px;
            font-family: 'PFCentroSansPro-Regular';
        }
    }
    h3 {
        font-size: 36px;
        margin-bottom: 37px;
        @media (max-width:600px) {
            font-size: 24px;
            margin-bottom: 20px;
        }
    }
    .wrapper {
        position: relative;
        padding-bottom: 60px;
        &:before {
            content: '';
            display: block;
            position: absolute;
            width: 710px;
            height: 115px;
            background: url(../images/bg_a.png) no-repeat 0 0;
            
            bottom: 0;
            left: -336px;
            @media (max-width:600px) {
                transform: scale(0.5) translate(300px,60px )
            }
            
        }
        & > div {
            max-width: 670px;  
        }
    }
    form {
        max-width: 585px; 
        box-shadow: 0 0 18px rgba(0,0,0,.15);
    }
    .btn {
        @media (max-width:600px) {
            width: 100%;  
            font-size: 24px;
            text-align: center;
            padding: 16px;
        }
    }
}
form {
        padding: 38px 45px 52px 45px;
    background: #fff;
    color: $blue; 
    text-shadow: none;
    position: relative;
    text-align: left;
    @media (max-width:600px) {
        padding: 22px 17px;
    }
    &:after,
    &:before {
        content: ''; 
        width: 10px;
        height: 10px;
        background: $red;
        position: absolute;
        top: -10px;
        left: 0;
    }
    &:after {
        left: 15px;
        width: 200px;
    }
    p {
        color: $dblue;
        font-size: 20px; 
        margin-bottom: 39px;
        @media (max-width:600px) {
         font-size: 16px;
            margin-bottom: 20px; 
        }
    }
    b {
        font-weight: normal;
        font-size: 24px; 
        display: block;
        margin-bottom: 20px;
        @media (max-width:600px) {
         font-size: 18px;
        }
    }
    input {
        width: 100%;
        display: block;
    }
}

input {
    border: none;
    background: #edf1f7;
        padding: 11px 5px 12px 25px;
    margin-bottom: 20px; 
    
    font-size: 24px;
    @include plhl() {
        color: #878d96;
    }
    @media (max-width:600px) {
        padding: 14px 10px; 
    }
}

.section {
    &_head {
        font-size: 60px;
        font-family: $black; 
        margin-bottom: 40px; 
        @media (max-width:780px) {
            font-size: 48px;
            line-height: 48px;
        }
        @media (max-width:580px) {
            font-size: 35px;
            line-height: 35px;
        }
        
        span {
            color: $red; 
            display: inline-block;
        }
        b {
            font-size: 30px;
            line-height: 60px;
        }
        p {
            color: $red;
            font-size: 24px;
            font-family: 'PFCentroSansPro-Regular';
        }
    }
    &_lr {
        display: flex;
        justify-content: space-between;
        align-items: stretch;
    }
    .right,
    .left {
        width: calc( 50% - 15px );
    }
    &_blog {
        display: flex;
        justify-content: space-between;
        @media (max-width:880px) {
            display: block;
        }
        & > * {
            width: 100%;
        }
        .content {
            margin-right: 15px; 
        }
        aside {
            @media (min-width:880px) {
                min-width: 362px;
                max-width: 362px;
            }
        }
    }
    &_more {
        border-top: 1px solid #d7d7d7;
        margin-top: 25px;
        padding-top: 25px;
        &_index {
            margin: 0;
            padding: 0;
            border: none;
            .wrapper {
                border-bottom: 1px solid #d7d7d7;
                margin-bottom: 15px; 
            }
            
        }
        .blog {
            &_item_col {
                display: block!important;
            }
            &_img {
                margin-right: 0;
                img {
                    min-width: 100%;
                    max-width: 100%;
                }
            }
        }
        &_head {
            font-size: 24px;
            font-family: $bold;
            
            display: flex;
            justify-content: space-between;
            align-items: center;
            
            margin-bottom: 25px;
            padding-left: 23px;
            
            position: relative;
            &:before {
                content: '';
                position: absolute;
                width: 15px;
                height: 16px;
                background: #da2128;
                display: block;
                left:0px;
                bottom: 7px;
            }
        }
    }
}

.main {
    display: flex;
    flex-direction: column;
    height: 100%;
}
.page {
    height: 100%;
}

.form {
    &_s {
        &_btn {
            padding-top: 16px;
            text-align: right;
            .btn {
                width: 283px;
            }
        }
    }
    &_big {
        padding: 0;
        &:after,
        &:before {
            display: none;
        }
        &_row {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            @media (max-width:780px) {
                display: block;
            }
            & > * {
                width: 48%;
                @media (max-width:780px) {
                    width: 100%;
                }
            }
        }
        &_ch {
            .form_big_row {
                justify-content: flex-start;
                @media (max-width:780px) {
                    display: flex;
                }
                > * {
                    width: auto;
                    margin-right: 25px;
                }
            }
        }
        &_btn {
            text-align: right;
            margin-bottom: 40px;
            .btn {
                padding: 13px 28px;
                &.grey {
                    background: #aeaeae;
                    margin-right: 15px;
                }
            }
        }
        label {
            margin-bottom: 24px;
            display: block;
            cursor: pointer;
            & > div {
                font-size: 18px;
                color: $blue;
                margin-bottom: 15px;
            }
        }
        input {
            margin-bottom: 0;
            font-size: 18px;
            &[type="radio"],
            &[type="checkbox"] {
                width: 0;
                height: 0;
                margin-right: 25px;
                display: inline-block;
                position: relative;
                &:checked {
                    &:after {
                        position: relative;
                        
                    }
                    &:before {
                                content: '';
                                display: block;
                                position: absolute;
                                width: 16px;
                                height: 16px;
                                border-radius: 50%;
                                background: #da2128;
                                top: -13px;
                                left: 5px;
                        }
                }
                &:after {
                    content: '';
                    display: inline-block;
                    width: 26px;
                    height: 26px;
                    border: 2px solid $blue;
                    vertical-align: top;
                    margin-top: -18px; 
                    border-radius: 50%;
                }
            }
        }
        .label {
            &_head {
                font-size: 18px;
                color: $blue;
                margin-bottom: 15px;
            }
        }
        textarea {
            border: none;
            background: #edf1f7;
            padding: 16px 5px 16px 25px;
            margin-bottom: 20px; 
            font-size: 18px;
            width: 100%; 
            
            min-height: 101px;
        }
    }
}

.lng {
    &_head {
        &:hover {
            
        }
    }
    &_dd {
        display: none;
        padding: 10px;
        background: #f7f7f7;
        top: 99%;
        position: absolute;
        left: 0;
        z-index: 5;
        text-align: center;
        right: 0;
        a {
            display: block;
            color: $blue;
            font-family: $bold;
            font-size: 24px;
            text-decoration: none;
            white-space: nowrap;
            &:hover {
                color: #da2128;
            }
        }
    }
}
